/* cookie message */

.lgk-cookie-message {
    box-sizing: border-box;
    font-family: "HK Grotesk", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    position: absolute;
    z-index: 1999;
    background: rgba(255,255,255,.95);
    -webkit-backdrop-filter: saturate(180%) blur(20px);
    backdrop-filter: saturate(180%) blur(20px);
    box-shadow: 0 3px 36px rgba(0,0,0,.1);
    border-radius: 1rem;
    bottom: 1rem;
    right: 1rem;
    width: 240px;
    /* right: 0; */
    padding: 1rem;
    animation: message-in .7s;
}

.lgk-cookie-message a {
    text-decoration: none;
    color: rgb(99, 128, 179) !important;
}
.lgk-cookie-message a:hover {
    text-decoration: underline;
    color: rgb(99, 128, 179) !important;
}

.lgk-cookie-message button {
    cursor: pointer;
    display: block;
    width: 100%;
    font: inherit;
    border: none;
    border-radius: 100px;
    background-color: #8EA6D0;
    color: white;
    margin-top: 1rem;
    padding: .5rem .7rem;
}
.lgk-cookie-message button:hover {
    background-color: #6a8ac1;
}

@keyframes message-in {
    from {
        margin-top: -100%;
    }
}

/* pill */

.lgk-pill {
    position: fixed;
    z-index: 2001;
    top: -140px;
    left: .7rem;
    right: .7rem;
    display: flex;
    overflow: hidden;
    border-radius: 1rem;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: top .2s;
    -webkit-backdrop-filter: saturate(180%) blur(20px);
    backdrop-filter: saturate(180%) blur(20px);
}

.lgk-pill.in, .lgk-pill.hover {
    box-shadow: 0 3px 36px rgba(0,0,0,.3);
}

.lgk-pill-content {
    border: none;
    overflow: hidden;
    width: 100%;
    height: 140px;
    background-color: rgba(255,255,255,.7);
    -webkit-backdrop-filter: saturate(180%) blur(20px);
    backdrop-filter: saturate(180%) blur(20px);
    margin: 0;
    z-index: 2000;
}
@media (prefers-color-scheme: dark) {
    .lgk-pill-content {
        background-color: rgba(21,29,34,.7);
    }
}

.lgk-pill.in .lgk-pill-content {
    background-color: transparent;
}

.lgk-pill-btn {
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    z-index: 2001;
    top: 0;

    display: flex;
    justify-content: center;
    align-items: center;
    width: 48px;
    height: 48px;
    cursor: pointer;
    color: rgba(255,255,255,.7);
    transition: color .2s, margin-top .2s, top .2s;
}

.lgk-pill-btn.in {
    top: calc(140px + .7rem - 48px + .7rem);
}

@media (hover: hover) {
    .lgk-pill-btn:hover, 
    .lgk-pill-btn:active {
        color: white;
        margin-top: 10px;
    }

    .lgk-pill-btn.in:hover {
        margin-top: -10px;
    }
}

.lgk-pill-btn.black {
    color: rgba(0,0,0,.7);
}
.lgk-pill-btn.black:hover, 
.lgk-pill-btn.black:active {
    color: black;
}

@media (prefers-color-scheme: light) {
    .lgk-pill-btn.auto-theme {
        color: rgba(0,0,0,.7);
    }
    .lgk-pill-btn.auto-theme:hover, 
    .lgk-pill-btn.auto-theme:active {
        color: black;
    }
}

.lgk-pill-icon {
    display: inline-block;
    width: 100%;
    height: 5px;
    border-radius: 10px;
    background-color: currentColor;
    -webkit-backdrop-filter: saturate(180%) blur(5px);
    backdrop-filter: saturate(180%) blur(5px);
}

.lgk-pill-wrapper {
    position: fixed;
    z-index: 1999;
    top: 0; right: 0; left: 0;
}
.lgk-pill-wrapper.in {
    bottom: 0;
}